<template>
    <div class="game">
        <question class="game__pane" id="game" :question='this.activeQuestion'></question>
    </div>
</template>

<script>
import userData from '../fixtures/users';
import router from '../router';
import $ from 'jquery';
import question from '../components/Question.vue';

export default {
    name: 'HomePage',
    data: function() {
        return {
            userRoutes: '',
            activeQuestion: ''
        }
    },
    components: {
        question
    },
    methods: {
        runQuestion(questionKey, questionRoute) {
            this.activeQuestion = questionRoute;
            $("#game").fadeIn(250);
        },
        runFromCode(code) {
            this.runQuestion(code, this.userRoutes[code]);
        }
    },
    mounted: function () {
        if (localStorage.getItem('easter-user-name') === null) {
            router.push('/');
        }

        userData.activeUser = localStorage.getItem('easter-user-code');
        
        document.querySelector('meta[name="theme-color"]').setAttribute('content',  '#C8E9A0');

        this.userRoutes = userData.users[userData.activeUser].routes;
        this.runFromCode('start');
    }
}
</script>

<style lang="scss" scoped>
    @keyframes welcome {
        0% {
            opacity: 0;
        }
        20% {
            opacity: 1;
        }
        80% {
            opacity: 1;
        }
        100% {
            opacity: 0;
        }
    }
    .game {
        background-color: #C8E9A0;
        // font-size: 24px;
        font-family: 'Roboto Mono', monospace;
        height: 100%;
        flex-direction: column;
        padding: 20px;
        box-sizing: border-box;
        &__welcome {
            font-size: 24px;
            font-family: 'Roboto Mono', monospace;
            opacity: 0;
            background-color: inherit;
            top:0;
            left:0;
            height:100%;
            width:100%;
            position: fixed;
            animation: welcome 5s 1;
            display: flex;
            align-items: center;
            justify-content: center;
            padding: 20px;            
            z-index: 10;
        }
        &__pane {
            display: none;
        }
    }
</style>