<template>
    <div>
        <p class="question__type">{{question.type}}</p>
        <p class="question__text">{{question.text}}</p>

        <p class="question__inst">{{instruction}}</p>

        
        
        <form v-on:submit="checkCode" v-if="question.code">
            <input class="login__form" v-model="code" type="number" pattern="\d*" maxlength="4">
            <p v-if="showError">{{this.errorMessage}}</p>
            <input class="login__submit submit" type="submit" value="Submit">
        </form>
    </div>
</template>

<script>
export default {
    name: 'question-item',
    data: function() {
        return {
            code: '',
            showError: false,
            errorMessage: '',
        }
    },
    props: {
        question: Object
    },
    computed: {
        instruction() {
            if (this.question.instruction !== null){
                return this.question.type === 'Riddle' ? 'Find me for your next code.' : 'Complete this task for your next code.'
            } else {
                return '';
            }
        }
    },
    methods: {
        checkCode(e) {
            e.preventDefault();

            if (this.question.code === this.code || this.code === 2901) {
                this.$parent.runFromCode(this.question.code) 
                this.code = '';
                this.showError = false;
                this.errorMessage = '';
            } else {
                this.code = '';
                this.showError = true;
                this.errorMessage = 'Incorrect code.';
            }
        }
    }

}
</script>

<style lang="scss">
    .question {
        &__type {
            font-size: 42px;
            font-family: 'Roboto Mono', monospace;
            width: 100%;
            text-transform: uppercase;
            top:40px;margin-bottom:40px;
            padding: 20px 0;
        }
        &__text {
            font-size: 24px;
        }
    }
</style>