<template>
    <div class="pre-game">
        <div class="pre-game__welcome">{{this.welcomeMessage}}</div>
        <div class="pre-game__instruction" v-if="!userIsCodeGiver && dayIsValid">
            <p>
                That pesky double agent Nigel is at it again! You've saved London from his antics before, but can you save Easter?<br><br>

            You will recieve a series of tasks and riddles on your mission to stop Nigel. 
            Each riddle will lead you to a code to continue your mission, and intelligence will give you a code after completing each task.<br><br>
            
            If you get stuck, our Head of Intelligence Agent Jack is available, just summon him with the following message: 

            <br><br>
            <b>
            "Jack, Jack, you're super cool. I need your help because I'm a tool."
            </b><br><br>
            
            Good Luck, Agent {{this.agentName}}.
             </p>
            <input class="submit" type="submit" value="Start Mission" v-on:click="this.continue">

        </div>
        <div class="pre-game__instruction" v-if="userIsCodeGiver && dayIsValid">
            <p>Codes to give out for each task.</p><br>
            <table>
                <tbody>
                    <tr>
                        <td>Carla made Rachel laugh</td>
                        <td>7569</td>
                    </tr>
                    <tr>
                        <td>Rachel showed you something you can twist</td>
                        <td>7004</td>
                    </tr>
                    <tr>
                        <td>Carla showed you something you can squeeze</td>
                        <td>6206</td>
                    </tr>
                    <tr>
                        <td>Rachels drawing was identified</td>
                        <td>3214</td>
                    </tr>
                    <tr>
                        <td>Carlas drawing was identified</td>
                        <td>1842</td>
                    </tr>
                    <tr>
                        <td>Rachel made Carla laugh</td>
                        <td>8625</td>
                    </tr>
                </tbody>
            </table>
        </div>

        <div class="pre-game__instruction" v-if=!dayIsValid>
            <p>It's not Easter yet you eager beaver</p>
        </div>
    </div>
</template>

<script>
import userData from '../fixtures/users';
import router from '../router';
import $ from 'jquery';

export default {
    name: 'HomePage',
    data: function() {
        return {
            userRoutes: '',
            activeQuestion: ''
        }
    },
    computed: {
        welcomeMessage() {
            return `Welcome ${localStorage.getItem('easter-user-name') ?? ''}...`;
        },
        agentName() {
            return localStorage.getItem('easter-user-surname') ?? ''
        },
        userIsCodeGiver() {
            return userData.users[userData.activeUser].codeGiver;
        },
        dayIsValid() {
            return new Date().getDay() === 7
        }
    },
    methods: {
        continue() {
            router.push('/game')
        }
    },
    mounted: function () {
        if (localStorage.getItem('easter-user-name') === null) {
            router.push('/');
        }
        setTimeout(function(){
            $(".pre-game__instruction").fadeIn(250);  
        },4500)

        userData.activeUser = localStorage.getItem('easter-user-code');
        
        document.querySelector('meta[name="theme-color"]').setAttribute('content',  '#f7a278');

    }
}
</script>

<style lang="scss" scoped>
    @keyframes welcome {
        0% {
            opacity: 0;
        }
        20% {
            opacity: 1;
        }
        80% {
            opacity: 1;
        }
        100% {
            opacity: 0;
        }
    }
    .pre-game {
        background-color: #f7a278;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        padding: 20px;
        box-sizing: border-box;
            font-family: 'Roboto Mono', monospace;
        &__welcome {
            font-size: 24px;
            opacity: 0;
            background-color: inherit;
            top:0;
            left:0;
            height:100%;
            width:100%;
            position: fixed;
            animation: welcome 5s 1;
            display: flex;
            align-items: center;
            justify-content: center;
            padding: 20px;            
            z-index: 10;
            pointer-events: none;
        }
        &__instruction {
            display: none;
        }
    }

    table {
        text-align: left;
        border-collapse: collapse
    }
    
    
    td {
        border: 2px solid black;
        padding: 15px;
    }
</style>