<template>

    <div class="login">
        <p>Please enter your access code:</p>
        <form v-on:submit="checkCode">
            <input class="login__form" v-model="code" type="number" pattern="\d*" maxlength="4">
            <p v-if="showError">{{this.errorMessage}}</p>
            <input class="login__submit submit" type="submit" value="Submit">
        </form>

    </div>
</template>


<script>

import userData from '../fixtures/users';
import router from '../router';

export default {
    name: 'LoginPage',
    data: function() {
        return {
            code: '',
            showError: false,
            errorMessage: '',
        }
    },
    methods: {
        checkCode(e) {
            e.preventDefault();

            if (userData.users[this.code]) {
                userData.activeUser = this.code;
                localStorage.setItem('easter-user-code', this.code)
                localStorage.setItem('easter-user-name', userData.users[this.code].name)
                localStorage.setItem('easter-user-surname', userData.users[this.code].surname)
                router.push('/welcome')
            } else {
                this.code = '';
                this.showError = true;
                this.errorMessage = 'Incorrect access code.';
            }
        }
    }
}
</script>

<style lang="scss">

    .login {
        position: relative;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        padding: 20px;
        box-sizing: border-box;
        background-color: #6dd3ce;
        font-size: 16px;
        @media (min-width: 552) {
            padding: 40px;
        }

        & form {
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
        }

        &__form {
            background-color: rgba(black, .1);
            border-radius: 10px;
            border:none;
            outline:none;
            padding: 20px 10px;
            margin-top:20px;
            font-size: 24px;
            width: 240px;
            box-sizing: border-box;
            // text-align:center;
            letter-spacing: 30px;
            text-indent: 30px;
            text-align:center;
        }

        &__submit {
            display: block;
            margin-top: 20px;
            font-size: 16px;
            padding: 20px 40px;

        }
    }

</style>